"use strict"
window.addEventListener('DOMContentLoaded', () => {

const slider = document.querySelector('.hero__slider');
const slides = document.querySelectorAll('.hero__slide');
const prev = document.querySelector('.hero__prev');
const next = document.querySelector('.hero__next');
const current = document.querySelector('.hero__current-slide');
const total = document.querySelector('.hero__all-slides');
const image = document.querySelector('.hero__wrapper');

let offset = 0;
let slideIndex = 1;

if (slides.length < 10) {
  total.textContent = `0${slides.length}`;
  current.textContent =  `0${slideIndex}`;
} else {
  total.textContent = slides.length;
  current.textContent =  slideIndex;
}

next.addEventListener('click', () => {

  if (offset == (770 * (slides.length - 1))) {
      offset = 0;
  } else {
      offset += 770;
  }

  image.style.left = `-${offset}px`;

  if (slideIndex == slides.length) {
    slideIndex = 1;
  } else {
    slideIndex++;
  }

  if (slides.length < 10) {
    current.textContent =  `0${slideIndex}`;
} else {
    current.textContent =  slideIndex;
}
});

prev.addEventListener('click', () => {

  if (offset == 0) {
    offset = (770 * (slides.length - 1));
  } else {
      offset -= 770;
  }

  image.style.left = `-${offset}px`;

  if (slideIndex == 1) {
    slideIndex = slides.length;
  } else {
    slideIndex--;
  }

  if (slides.length < 10) {
    current.textContent =  `0${slideIndex}`;
} else {
    current.textContent =  slideIndex;
}
});


});





